.table {
  .cellWrapper {
    display: flex;
    align-items: center;

    .image {
      width: 32px;
      height: 32px;
      border-radius: 50%;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      -ms-border-radius: 50%;
      -o-border-radius: 50%;
      margin-right: 10px;
      object-fit: cover;
    }
  }

  .status{
      padding: 5px;
      border-radius: 5px;
      -webkit-border-radius: 5px;
      -moz-border-radius: 5px;
      -ms-border-radius: 5px;
      -o-border-radius: 5px;

      &.Approved{
          color: green;
          background-color: rgba(0, 128, 0, 0.151);
      }
      &.Pending{
        color: goldenrod;
        background-color: rgba(189, 189, 3, 0.103);
    }
}
}
